import React, { PureComponent } from "react";
import { Container, Row, Col, Button } from 'reactstrap';

import { Link } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleCheck , faTriangleExclamation } from "@fortawesome/free-solid-svg-icons";

class DropboxCallback extends PureComponent {

  constructor(props) {
    super(props);

    this.state = {
      callbackUID: null,
      isError: false
    }
  }

  componentDidMount() {
    const callbackUID = this.props.match.params.token;

    if (callbackUID === null || callbackUID === undefined) {
      this.setState({ isError: true });
    } else {
      this.setState({ callbackUID });
    }
  }

  render() {
    const { isError, callbackUID } = this.state;
    return (
      <Container>
        <div className="jumbotron mt-5">

          {!isError ? (
            <>
              <div className="text-center">
                <FontAwesomeIcon icon={faCircleCheck} size="3x" style={{ color: 'green' }} />
                <h2 className="mt-4">Integration with your Dropbox account completed</h2>
              </div>

              <Row className="mt-4">
                <Col md="4">
                  <div className="card">
                    <div className="card-header font-weight-bold">
                      1. Copy / move files to app folder
                    </div>

                    <div className="card-body">
                      First step, copy all the files you want to migrate to 
                      IPFS in the folder named Applications/Send-To-IPFS
                    </div>
                  </div>
                </Col>

                <Col md="4">
                  <div className="card">
                    <div className="card-header font-weight-bold">
                      2. Your files will be hosted on IPFS
                    </div>

                    <div className="card-body">
                      Our application will automatically detect all the files you add to send them over IPFS,
                      without deleting the original
                    </div>
                  </div>
                </Col>    

                <Col md="4">
                  <div className="card">
                    <div className="card-header font-weight-bold">
                      3. Check migrated files and status
                    </div>

                    <div className="card-body">
                      Using the identifier below, you can access to the application
                      to check all files that were migrated and their status
                    </div>
                  </div>
                </Col> 
              </Row>

              <Row className="mt-4">
                <Col md="12">
                  {callbackUID}
                </Col> 
              </Row>
            </>

          ) : (
            
            <>
              <div className="text-center">
                <FontAwesomeIcon icon={faTriangleExclamation} size="3x" style={{ color: 'orange' }} />
                <h2 className="mt-4">Something wrong during authentification flow</h2>
              </div>

              <Row className="mt-4">
                <Col md="12">
                  <div className="card">
                    <div className="card-body text-center">
                      It seems an error occured when validating your authentification to your Dropbox
                      account. Please try again or create an issue on our Github repository
                      <br /><br />
                      <a href="https://github.com/FlorianRuen/Dropbox-To-IPFS-App">https://github.com/FlorianRuen/Dropbox-To-IPFS-App</a>
                    </div>
                  </div>
                </Col>
              </Row>

              <Row className="text-center mt-4">
                <Col md="12">
                  <Button tag={Link} to="/" color="warning" size="3x">
                    <span className="as--light">
                      Try authentification again
                    </span>
                  </Button>
                </Col>
              </Row>
            </>

          )}

        </div>
      </Container>
    );
  }
}

export default DropboxCallback;